import Vue from 'vue'
import Router from 'vue-router'

// Rotas do site
const Barbearia = () => import('@/pages/site/barbearia/Barbearia')
const HomeSite = () => import('@/pages/site/home/Home')
const SiteChangePassword = () => import('@/pages/site/change-password/ChangePassword')
const SiteSchedule = () => import('@/pages/site/schedule/Schedule')
const SiteScheduleDetail = () => import('@/pages/site/schedule/Detail')

// Rotas autenticacao
const Login = () => import('@/pages/auth/Login')
const Register = () => import('@/pages/auth/Register')
const BarberChangePassword = () => import('@/pages/auth/barber/ChangePassword')
const	RecoveryPassword = () => import('@/pages/auth/RecoveryPassword')
const BarberConfirmRegister = () => import('@/pages/auth/barber/ConfirmRegister')
const UserConfirmRegister = () => import('@/pages/auth/user/ConfirmRegister')

// Rotas Admin
const Barbershop = () => import('@/pages/admin/Barbershop')
const Employee = () => import('@/pages/admin/Employee')
const Help = () => import('@/pages/admin/Help')
const Plans = () => import('@/pages/admin/Plans')
const Profile = () => import('@/pages/admin/Profile')
const Service = () => import('@/pages/admin/Service')
const Schedule = () => import('@/pages/admin/Schedule')
const Approval = () => import('@/pages/admin/Approval')

const routes = [
  // ADMIN
  { path: '/admin', component: Schedule, name: 'admin', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/minha-barbearia', component: Barbershop, name: 'admin-barbershop', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/funcionarios', component: Employee, name: 'admin-employee', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/ajuda', component: Help, name: 'admin-help', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/planos', component: Plans, name: 'admin-plans', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/meu-perfil', component: Profile, name: 'admin-profile', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/meus-servicos', component: Service, name: 'admin-service', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/minha-agenda', component: Schedule, name: 'admin-schedule', meta: { requiresAuth: true, isAdmin: true } },
	{ path: '/admin/aguardando-aprovacao', component: Approval, name: 'admin-approval', meta: { requiresAuth: true, isAdmin: true } },
  // SITE
	{ path: '/alterar-senha', component: SiteChangePassword, name: 'site-change-password', meta: { requiresAuth: true, isAdmin: false } },
	{ path: '/meus-agendamentos', component: SiteSchedule, name: 'site-schedule', meta: { requiresAuth: true, isAdmin: false } },
	{ path: '/agendamento/:id', component: SiteScheduleDetail, name: 'site-schedule-detail', meta: { requiresAuth: true, isAdmin: false } },
	{ path: '/', component: HomeSite, name: 'home', meta: { isAdmin: false } },
  { path: '/barbearia/:id/:url', name: 'site-barbershop', component: Barbearia, meta: { isAdmin: false } },
  // AUTH
  { path: '/login', name: 'login', component: Login, meta: { isAdmin: false } },
  { path: '/cadastro', name: 'register', component: Register, meta: { isAdmin: false } },
	{ path: '/confirmar-cadastro/:token', name: 'user-confirm-register', component: UserConfirmRegister, meta: { isAdmin: false } },
	{ path: '/barbeiro/confirmar-cadastro/:token', name: 'barber-confirm-register', component: BarberConfirmRegister, meta: { isAdmin: false } },
  { path: '/recuperar-senha', name: 'recovery-password', component: RecoveryPassword, meta: { isAdmin: false } },
  { path: '/barbeiro/alterar-senha/:token', name: 'barber-change-password', component: BarberChangePassword, meta: { isAdmin: false } },
]

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
	let tokenValidado = false

	if (to.meta.requiresAuth) {
		let token = to.meta.isAdmin ? localStorage.token : localStorage.userToken
		if (token) {
			let partesToken = token.split('.')

			if (partesToken.length === 3) {
				try {
					let payloadToken = JSON.parse(atob(partesToken[1]))
					let date = Math.floor(Date.now() / 1000)
					let expDate = Math.floor(new Date (payloadToken.data_expiracao) / 1000)
					if (expDate > date) tokenValidado = true
				} catch (err) { tokenValidado = false }
			}
		}
	} else tokenValidado = true

	if (tokenValidado) next()
	else {
		if (to.meta.isAdmin) localStorage.routePathAdmin = to.path
		else localStorage.routePathSite = to.path
		window.location = window.location.origin + '/login'
	}
})

export default router
